<template>
  <div>
  <h3 class="news-title">{{$route.params.news[$route.params.index].title}}</h3>
    <span class="news-time">发布时间：{{$route.params.news[$route.params.index].time}}</span>
    <p class="news-content" v-html="aaa"></p>

<router-link  :to="{name:'News',params:{news2}}" v-if="list>0">上一个新闻：aaaaa</router-link>
<div v-else>没有上一个了</div>
<router-link :to="{path:'home'}" v-if="list<$route.params.news.length-1">下一个新闻：{{$route.params.news[$route.params.index+1].title}}</router-link>
    <div v-else>没有下一个了</div>
<div @click="back">返回</div>
  </div>
</template>
<script>
  export default {
    data(){
      return{
        news2:this.$route.params.news,
        list:this.$route.params.index,
        aaa:this.$route.params.news[this.$route.params.index].text
      }
    },
  methods:{
    back(){
      this.$router.go(-1)
    }
  }
  }
</script>
<style>
h3.news-title{ font-size: 18px; margin: 12px;}
span.news-time{ font-size: 12px; color: #cccccc;}
  p.news-content{ text-align: left; padding: 14px;}
</style>



