import Vue from 'vue'
import Router from 'vue-router'
import Home from '../pages/Home'
import About from '../pages/about'
import Course from '../pages/course'
import Job from '../pages/job'
import Our from '../pages/our'
import Teacher from '../pages/teacher'
import FromSub from '../pages/From'
import News from '../pages/news'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',//分配的路由地址
      component: Home
    },
    {
      path: '/home',//分配的路由地址
      component: Home
    },
    {
      path: '/About',//分配的路由地址
      component: About
    },
    {
      path: '/Course',//分配的路由地址
      component: Course
    },
    {
      path: '/Job',//分配的路由地址
      component: Job
    },
    {
      path: '/Our',//分配的路由地址
      component: Our
    },
    {
      name:'te',
      path: '/Teacher',//分配的路由地址
      component: Teacher
    },
    {
      path: '/From',//分配的路由地址
      component: FromSub
    },
    {
      name: 'News',//分配的路由地址
      path: '/News',//分配的路由地址
      component:News
    }
  ]
})



