<template>
  <div class="foot-nav">
    <router-link :to="{path:'/from'}"><span><img src="../assets/images_home/tab_iocn_62.png">申请试听</span></router-link>
    <span @click="tel"><img src="../assets/images_home/tab_iocn_64.png">联系电话</span>
    <span><img src="../assets/images_home/tab_iocn_66.png">联系我们</span>
</div>

</template>
<script>
  import { MessageBox } from 'mint-ui';
  export default{
    data(){
      return{
        num:'88888888'
      }
    },
    methods:{
      tel(){
        MessageBox({
          title: '',
          message:'<div style="color:red">'+this.num+'</div>',
          confirmButtonText:'拨打电话'
        }).then(action => {
          window.location.href='tel://'+this.num
      });
      }
    }
  }
</script>
<style>


  .foot-nav{ display: flex; justify-content: space-around; height: 49px; border-bottom: 1px solid #ccc; align-items: center; position: absolute; bottom: 0px; width: 100%; background: #c01920; color: #ffffff; font-size: 10px;}
  .foot-nav img{  height: 20px; margin: 6px auto;}
</style>



